  <Top_bar></Top_bar>
  <My_header></My_header>
  <Page_decoration></Page_decoration>
</template>

<script>
import My_header from "@/components/my_header";
import Top_bar from "@/components/top_bar";
import Page_decoration from "@/components/page_decoration";

export default {
  name: 'App',
  data(){
    return{
      publicPath: process.env.BASE_URL
    }
  },
  components: {
    Page_decoration,
    Top_bar,
    My_header
  },
  created() {
    console.log(window.location)
    var base_url=window.location.href.split(/[?#]/)[0].replace('index.html','')
    setTimeout(() => {
      window.L2Dwidget.init({
        pluginRootPath: base_url+'static/live2dw/',
        pluginJsPath: 'lib/',
        pluginModelPath: 'live2d-widget-model-haru_2/assets/', //中间这个haru_2就是你的老婆,想换个老婆,换这个就可以了
        tagMode: false,
        debug: false,
        model: { jsonPath: base_url+'static/live2dw/live2d-widget-model-haru_2/assets/haru02.model.json' },
        display: { position: 'left', width: 140, height: 320 },  //调整大小,和位置
        mobile: { show: true },   //要不要盯着你的鼠标看
        log: false,
      })
    }, 1000)
  }
}
</script>

