import axios from "axios";
const base1=window.location.origin

const require=axios.create({
    baseURL:base1+'/WeiXin/point'
})
function get_loc_list(){
    return require.get('/selectAllPoints')
}
function signal_search(data){
    return require.get('/selectPointByCondition',{
        params:data
    })
}
function add_loc(data){
    console.log('我发送了',data)
    return require.get('/addPoint',{
        params:data})
}
function update(data){
    return require.get('/updatePoint',{
        params:data
    })
}
function selectPointByName(name) {
    return require.get('/selectPointByName',{
        params:{name}
    })
}

function delete_loc(item){
    return require.get('/deletePointByName?',{
        params:{name:item.name}
    })
}
export { get_loc_list, signal_search, add_loc, update, delete_loc,selectPointByName}