<template>
  <div>
  <div class="bg_container">
  </div>
    <div class="on">

      <img src="../assets/kairo_00.gif" id="kairo" :style="right" draggable="false">
      <img src="../assets/cloud04.png" id="cloud1" class="cloud">
      <img src="../assets/cloud05.png" id="cloud2" class="cloud">
      <To_drag :i_x="1050" :i_y="100">
        <img id="fly1" src="../assets/top6.gif" draggable="false">
      </To_drag>
    </div>
  </div>
</template>

<script>
import To_drag from "@/components/to_drag";
export default {
  name: "page_decoration",
  components: {To_drag},
  data(){
    return{
      isRight:false
    }
  },
  computed:{
    right(){
      if (!this.isRight) return {transform:`RotateY(180deg)` }
      else return {transform:`RotateY(0deg)` }
    }
  },
  mounted() {
    setInterval(()=>{
      this.isRight=!this.isRight
    },10000)
  }
}
</script>

<style scoped>
img{
  position: fixed;
  top: 0;
}
#fly1{
  top: initial;
}
#kairo{
  /*top: 20px;*/
  /*left: 0;*/
  animation-name: move;
  animation-duration: 10s;
  animation-direction: alternate-reverse;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes go-right {
  from {translate: 0}
  to{translate: 60%}
}
@keyframes move {
  from {translate: 0 0}
  to{translate: 600px 0}
}
.cloud{
  animation-name: go-right;
  animation-duration: 10s;
  animation-direction: alternate-reverse;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  pointer-events: none;
}
#cloud1{
  left: 300px;
}
#cloud2{
  left: 0;
  top: 500px;
}
.on{
  width: 100%;
  height: 100%;
  top: 0;
}
.bg_container{
  width: 100%;
  height: 1000px;
  background-image: url('../assets/all_bg02.png');
  background-size: 100% 100%;
  background-repeat: repeat;
  position: fixed;
  z-index: -1;
  top: 0;
}
</style>