<template>
  <div class="big" @mousedown="start" :style="{left:x+'px',top:y+'px'}">
    <slot>
    </slot>
  </div>
</template>

<script>
var start_pos={x:0,y:0}
var start_pos_div={x:0,y:0}
export default {
  name: "to_drag",
  data(){
    return{
      x:this.i_x,
      y:this.i_y
    }
  },
  props:{
    i_x:Number,
    i_y:Number
  },
  methods:{
    start(e){
      start_pos.x=e.clientX
      start_pos.y=e.clientY
      start_pos_div.x=this.x
      start_pos_div.y=this.y
      window.onmousemove=(e)=>{
        this.x=e.clientX-start_pos.x+start_pos_div.x
        this.y=e.clientY-start_pos.y+start_pos_div.y
      }
      window.ontouchmove=(e)=>{
        e.preventDefault()
        this.x=e.touches[0].clientX-start_pos.x+start_pos_div.x
        this.y=e.touches[0].clientY-start_pos.y+start_pos_div.y
      }

      window.onmouseup=()=>{
        window.onmousemove=null
      }
      window.ontouchend=()=>{
        window.onmousemove=null
      }
    }
  }
}
</script>

<style scoped>
.big{
  position: fixed;
}
</style>