<template>
  <Search_bar @show_search_result="show_search_result" @show_all="get_loc"></Search_bar>
  <table>
    <tr>
      <th>名字</th>
      <th>纬度</th>
      <th>经度度</th>
      <th>url</th>
      <th>tip</th>
      <th>操作</th>
    </tr>
    <tr v-for="item in location" :key="item.name">

      <td>{{item.name}}</td>
      <td>{{item.longitude}}</td>
      <td>{{item.latitude}}</td>
      <td>{{item.url}}</td>
      <td>{{item.tips}}</td>
      <td>
        <button class="b_change" @click="open_small_window(item)">修改</button>
        <button class="b_remove" @click.right.prevent="delete_loc(item)">删除</button>
      </td>

    </tr>
  </table>
  <div class="add" @click="add">+</div>
<To_drag :i_x="center_x/3" :i_y="center_y-250">
  <div class="small-window" v-show="isShowSmallWindow">
    <form>
      <label>名字</label><input type="text" v-model="current_show.name"><br>
      <label>经度</label><input type="text" v-model="current_show.longitude"><br>
      <label>纬度</label><input type="text" v-model="current_show.latitude"><br>
      <label>tip</label><input type="text" v-model="current_show.tip"><br>
      <label>url</label><input type="text" v-model="current_show.url"><br>
      <button @click.prevent="close_small_window">关闭</button>
      <button type="submit" @click.prevent="submit_update">提交</button>
    </form>

  </div>
</To_drag>
</template>

<script>
import To_drag from "@/components/to_drag";
import Search_bar from "@/components/Search_bar";
import * as require from '../require/api'
export default {
  name: "my_header",
  components: {Search_bar, To_drag},
  computed:{
    center_x(){
      if (window.innerWidth<400)return 0
      return window.innerWidth/2
    },
    center_y(){
      return window.innerHeight/2
    },
  },
  data(){
    return{
      isAdd:false,
      isShowSmallWindow:false,
      current_show: {},
      location:[],
    }
  },

  created() {
    this.get_loc()
  },

  methods:{
    show_search_result(result){
      this.location=result
    },
    delete_loc(item){
      require.delete_loc(item).then(this.get_loc)
    },
    add(){
      this.current_show={id:'',name:'',longitude:'',latitude:'',url:'',tip:''}
      this.isShowSmallWindow=true
      this.isAdd=true
    },
    close_small_window(){
      this.isShowSmallWindow=false
      this.isAdd=false
    },
    open_small_window(item){
      this.isShowSmallWindow=true
      this.current_show={
         id:item.id, name: item.name, longitude: item.longitude, latitude: item.latitude, url:item.url,
        tip:item.tip
      }
    },
    submit_update(){
      if (this.isAdd){//添加的情况
        require.add_loc(this.current_show)
            .then(this.get_loc)
        this.close_small_window()
        return
      }
      //更新的情况
      require.update(this.current_show)
          .then(()=>{
            this.get_loc()
          })
      this.close_small_window()
    },
    get_loc(){
      require.get_loc_list()
          .then(res=>{
            this.location=res.data
          })
    },
  }
}

</script>

<style scoped>
:root{
  --simple-border:1px solid black
}
.add{
  position: fixed;
  bottom: 5%;
  left: 95%;
  width: 50px;
  height: 50px;
  background-color: #e2fabb;
  font-size: 30px;
  line-height: 50px;
  border-radius: 50%;
  box-shadow: grey 0 0 5px ;
}
.small-window{
  font-size: 20px;
  width: fit-content;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.53);
  backdrop-filter: blur(3px);
  padding: 40px 40px;
  border-radius: 30px;
  box-shadow: #a3e7ff 0 0 3px 3px;
}

.small-window button{
  margin:  0 20px;
}

.b_change{
  background-color: #e1ffb1;
}
.b_change:active,.b_change:hover{
  background-color: #ecffde;
}
.b_remove{
  background-color: #ff8b8b;
}
.b_remove:active,.b_remove:active{
  background-color: #ffbaba;
}
table{
  border-collapse: collapse;
  margin: 0 auto;
}

td,th{
  border: 1px solid black;
  width: 180px;
  padding: 2%;
  font-size: 16px;
  word-wrap: break-word;
}
th{
  background-color: #a3e7ff;
}
tr:nth-child(2n-1){
  background-color: #ffeedc;
}
tr:nth-child(2n){
  background-color: #ffffff;
}
tr:hover{
  background-color: rgb(203, 243, 199);
}
</style>