<template>
  <div class="container" v-show="isShow" ref="input" style="display: inline" @keydown.up="change_input_focus" @keydown.down="change_input_focus">

      名字<input type="text" v-model="search_content.name" @keydown.enter="signal_search('name')">
      <button class="go" @click="signal_search('name')">&#xe986;</button>
      <br>
      Tip  <input type="text" @keydown.enter="signal_search('tip')" v-model="search_content.tip">
      <button class="go" @click="signal_search('tip')">&#xe986;</button>
      <br>
      <button @click="multiply_search">两个一起查询</button>
    <p></p>
    <button id="showAll" @click="this.$emit('show_all')" style="display: inline">展示全部</button>
  </div>
    <button @click="isShow=!isShow">{{showText}}</button>
</template>

<script>
import {signal_search,selectPointByName} from "@/require/api";
export default {
  name: "Search_bar",
  computed:{
    showText(){
      if (this.isShow)return '隐藏'
      else return '显示'
    }
  },
  data(){
    return{
      isShow:true,
      search_content:{
        name:'',
        longitude:'',
        latitude:'',
        url:'',
        tip:''
      }
    }
  },
  methods:{
    change_input_focus(){
      const c=this.$refs.input.querySelectorAll('input')
      if (c[0]===document.activeElement)c[1].focus()
      else c[0].focus()
    },
    search_by_Name(){
      selectPointByName().then(res=>{
        this.$emit('show_search_result', res.data)
      })
    },
    signal_search(content) {
      let a = this.search_content[content]
      const para={}
      para[content]=a
      signal_search(para)
          .then(res => {
            this.$emit('show_search_result', res.data)
          })
    },
    multiply_search() {
      var keys=Object.keys(this.search_content)
      var param={}
      for (let i = 0; i < keys.length; i++) {
        if(this.search_content[keys[i]]){
          param[keys[i]]=this.search_content[keys[i]]
        }
      }

      signal_search(param)
          .then(res => {
            this.$emit('show_search_result', res.data)
          })
    }
  },
  emits:['show_search_result','show_all']
}
</script>

<style scoped>
#big_search_btn{
  width: 100px;
  text-align: center;
}
button{
  font-family: 'icomoon',serif ;;
}
.search{
  float: left;
}
.go{
  width: 40px;

}
</style>